<template>
    <div class="life">
        <div class="life-content container" v-if="postList.length>0">
            <section class="article_intro" v-for="post in postList" :key="post.id">
                <router-link :to="{ path: '/blog/post/'+post.id }">
                <div>
                    <h2>{{ post.title }}</h2>
                    <p>{{ post.add_time | time('YYYY-MM-DD') }}</p>
                    <div class="border"></div>
                </div>
                </router-link>
            </section>
        </div>
        <!-- SVG Loader -->
        <div v-else class="life-content container">
          <ContentLoader width='960' height='800'>
            <rect x="0" y="0" rx="3" ry="3" width="650" height="10" />
            <rect x="20" y="20" rx="3" ry="3" width="320" height="10" />
            <rect x="20" y="40" rx="3" ry="3" width="270" height="10" />
            <rect x="0" y="60" rx="3" ry="3" width="150" height="10" />
            <rect x="20" y="80" rx="3" ry="3" width="800" height="10" />
            <rect x="20" y="100" rx="3" ry="3" width="480" height="10" />
          </ContentLoader>
        </div>
    </div>
</template>


<script>
import {
    getAll,
} from '@/api/blog/index'
import gsap from 'gsap';
import { ContentLoader } from 'vue-content-loader'

export default {
    components: {
      ContentLoader
    },
    data(){
        return {
          lifeType: 3,
          postList: [], //
          page: 1,

        }
    },
    methods: {
      loadAnime(){
        gsap.from('.article_intro', {
          duration: 1,
          y: 10,
          opacity: 0,
          stagger: 0.06,
        })
      },
      loadArticle(){
        let fm = {};
        fm.page = this.page;
        fm.type = this.lifeType;
        fm.size = 20;
        getAll(fm).then(res=>{
          console.log(res);
          setTimeout(()=>{
            this.postList = res.results;
            this.$nextTick(()=>{
              this.loadAnime();
            })
          }, 300)
          
        })
      }
    },
    mounted(){
      this.loadArticle();
    }
}
</script>

